import Login from './components/Account/Login.vue'
import refresh from './components/refresh.vue'
import Home from './components/Home.vue'
import Homen from './components/Homen.vue'
import Homens from './components/Homens.vue'
import main from './components/Main/index.vue'
import articleEdit from './components/Main/edit.vue'
import articleAdd from './components/Main/add.vue'
import articleInfo from './components/Article/info.vue'
import articleInfon from './components/Article/infon.vue'
import me from './components/Me/index.vue'
import list from './components/Me/list.vue'
/**
 * meta
 * hideLeft: true
 * module: 
 * menu: 'menu''user'
 */

const routes = [
  {
    path: '/homen',
    component: Homen,
    children: [
      { path: '/article/infon/:id', component: articleInfo, name: 'articleInfonn', meta: { hideLeft: false, module: 'Main', menu: 'articleInfonn', title: '' }},
      { path: '/article/infonn/:id', component: articleInfo, name: 'articleInfonnn', meta: { hideLeft: false, module: 'Main', menu: 'articleInfonnn', title: '' }}
    ]
  },
  { path: '/', component: Login, name: 'Login', title: '' },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/refresh', component: refresh, name: 'refresh', title: '' }
    ]
  },
  {
    path: '/home',
    component: Homens,
    children: [
      { path: '/main', component: main, name: 'main', meta: { hideLeft: false, module: 'Main', menu: 'main', title: '' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/article/edit', component: articleEdit, name: 'articleEdit', meta: { hideLeft: false, module: 'Main', menu: 'main', title: '' }},
      { path: '/article/add/:id', component: articleAdd, name: 'articleAdd', meta: { hideLeft: false, module: 'Main', menu: 'main', title: '' }},
      { path: '/article/info/:id', component: articleInfo, name: 'articleInfo', meta: { hideLeft: false, module: 'Main', menu: 'main', title: '' }},
      { path: '/articlen/info', component: articleInfon, name: 'articleInfon', meta: { hideLeft: false, module: 'Main', menu: 'main', title: '' }}
    ]
  },
  {
    path: '/me',
    component: Home,
    children: [
      { path: '/me', component: me, name: 'me', meta: { hideLeft: false, module: 'Me', menu: 'me', title: '' }}
    ]
  },
  {
    path: '/me',
    component: Homens,
    children: [
      { path: '/me/list', component: list, name: 'list', meta: { hideLeft: false, module: 'Me', menu: 'me', title: '' }}
    ]
  }
]
export default routes



