import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import axios from 'axios'
import Lockr from 'lockr'
import Cookies from 'js-cookie'
import _ from 'lodash'
import moment from 'moment'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import routes from './routes'
import VueRouter from 'vue-router'
import store from './vuex/store'
import filter from './assets/js/filter'
import _g from './assets/js/global'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import 'assets/css/global.css'
import 'assets/css/base.css'
import 'assets/css/font-awesome.min.css'
import VueHtml5Editor from 'vue-html5-editor'

Vue.use(VueHtml5Editor, {
    // new VueHtml5Editor(options)
    // global component name
  name: 'vue-html5-editor',
    // 
    // if set true,will append module name to toolbar after icon
  showModuleName: false,
    // classfont-awesome
    // custom icon class of built-in modules,default using font-awesome
  icons: {
    text: 'fa fa-pencil',
    color: 'fa fa-paint-brush',
    font: 'fa fa-font',
    align: 'fa fa-align-justify',
    list: 'fa fa-list',
    link: 'fa fa-chain',
    unlink: 'fa fa-chain-broken',
    tabulation: 'fa fa-table',
    image: 'fa fa-file-image-o',
    hr: 'fa fa-minus',
    eraser: 'fa fa-eraser',
    undo: 'fa-undo fa',
    'full-screen': 'fa fa-arrows-alt',
    info: 'fa fa-info'
  },
    // 
    // config image module
  image: {
        //   max file size
    sizeLimit: 512 * 1024,
        // ,base64
        // upload config,default null and convert image to base64
    upload: {
      url: null,
      headers: {},
      params: {},
      fieldName: {}
    },
        // ,localResizeIMG,null
        // compression config,default resize image by localResizeIMG (https://github.com/think2011/localResizeIMG)
        // set null to disable compression
    compress: {
      width: 1600,
      height: 1600,
      quality: 80
    },
        // ,
        // handle response datareturn image url
    uploadHandler(responseText) {
            // default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
      var json = JSON.parse(responseText)
      if (!json.ok) {
        alert(json.msg)
      } else {
        return json.data
      }
    }
  },
    // en-uszh-cn
    // default en-us, en-us and zh-cn are built-in
  language: 'zh-cn',
    // 
  i18n: {
        // specify your language here
    'zh-cn': {
      'align': '',
      'image': '',
      'list': '',
      'link': '',
      'unlink': '',
      'table': '',
      'font': '',
      'full screen': '',
      'text': '',
      'eraser': '',
      'info': '',
      'color': '',
      'please enter a url': '',
      'create link': '',
      'bold': '',
      'italic': '',
      'underline': '',
      'strike through': '',
      'subscript': '',
      'superscript': '',
      'heading': '',
      'font name': '',
      'font size': '',
      'left justify': '',
      'center justify': '',
      'right justify': '',
      'ordered list': '',
      'unordered list': '',
      'fore color': '',
      'background color': '',
      'row count': '',
      'column count': '',
      'save': '',
      'upload': '',
      'progress': '',
      'unknown': '',
      'please wait': '',
      'error': '',
      'abort': '',
      'reset': ''
    }
  },
    // 
    // the modules you don't want
  hiddenModules: [],
    // 
    // keep only the modules you want and customize the order.
    // can be used with hiddenModules together
  visibleModules: [
    'text',
    'color',
    'font',
    'align',
    'list',
    'link',
    'unlink',
    'tabulation',
    'image',
    'hr',
    'eraser',
    'undo',
    'full-screen',
    'info'
  ],
    // examples
    // extended modules
  modules: {
        // omit,reference to source code of build-in modules
  }
})
// axios.defaults.baseURL = HOST
// axios.defaults.baseURL = 'http://192.168.3.19:8900/'
// axios.defaults.baseURL = 'http://192.168.1.106:9002'
// http://wdj.coolwx.com.cn/
axios.defaults.baseURL = 'http://wdj.coolwx.com.cn'
axios.defaults.timeout = 1000 * 15
axios.defaults.headers.authKey = Lockr.get('authKey')
axios.defaults.headers.sessionId = Lockr.get('sessionId')
axios.defaults.headers['Content-Type'] = 'application/json'

const router = new VueRouter({
  mode: 'history',
  // mode: 'hash',
  base: __dirname,
  routes
})

router.beforeEach((to, from, next) => {
  const hideLeft = to.meta.hideLeft
  store.dispatch('showLeftMenu', hideLeft)
  store.dispatch('showLoading', true)
  NProgress.start()
  next()
})

router.afterEach(transition => {
  NProgress.done()
})

Vue.use(ElementUI)
Vue.use(VueRouter)

window.router = router
window.store = store
window.HOST = HOST
window.axios = axios
window._ = _
window.moment = moment
window.Lockr = Lockr
window.Cookies = Cookies
window._g = _g
window.pageSize = 15

const bus = new Vue()
window.bus = bus

new Vue({
  el: '#app',
  template: '<App/>',
  filters: filter,
  router,
  store,
  components: { App }
// render: h => h(Login)
}).$mount('#app')



